.btn {
  box-shadow: none !important;
  outline: none !important;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {

  background: rgb(232, 196, 246);
}

#root {
  max-width: 800px;
  min-height: 100vh;
  margin: 0 auto;
  background: rgb(191, 225, 228);
}

.flex {
  display: flex !important;
}

.space-between {
  justify-content: space-between !important;
}

.center {
  align-items: center !important;
}

.ovh {
  overflow: hidden;
}

.nowrap {
  flex-wrap: nowrap;
}

.float-right{
  float: right;
}

.margin {
  margin: .5rem 1rem
}

.m10 {
  margin: 10px;
}

.mt10 {
  margin-top: 10px;
}

.mt20 {
  margin-top: 20px;
}

.mb10 {
  margin-bottom: 10px;
}

.mb20 {
  margin-bottom: 20px;
}

.mr5 {
  margin-right: 5px;
}

.ml5 {
  margin-left: 5px;
}

.w100 {
  width: 100%;
}

.tac {
  text-align: center;
}

.collapse {
  justify-content: flex-end;
}

.header {
  background: rgb(191, 225, 228);
}

.mw400 {
  max-width: 400px;
}

.cardHeader {
  position: relative;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.btn-refresh {
  position: absolute !important;
  right: 10px;
  top: 10px;

}

.btn-close {
  position: absolute !important;
  left: 10px;
  top: 10px;
}

.btn-close i {
  font-size: 1.3rem;
}

.btn-toggle-random {
  width: calc(100% - (1rem)*2);
}

.all-Item-list {
  height: 621px !important;
}

.pagination {
  justify-content: center;
  align-items: center;
}

.collapsedItem {
  max-height: 49px;
  height: 320px;
  animation: collapsedItem .3s linear;
}

.expandedItem {
  max-height: 500px;
  height: auto;
  animation: expandedItem .3s linear;
}

@keyframes expandedItem {
  from {
    max-height: 40px;
  }

  to {
    max-height: 500px;
  }
}

@keyframes collapsedItem {
  from {max-height: 320px}
  to {max-height: 49px;}
}





